<template>
    <a-card :bordered="false">
      <Result style="margin-bottom: 16px; margin-top: 48px" :is-success="false" :title="title" :description="description">
        <template slot="action">
          <a-button type="primary" @click="prevStep">返回修改</a-button>
        </template>
        <div>
          <div style="fontSize: 16px; color: rgba(0, 0, 0, 0.85); fontWeight: 500; marginBottom: 16px">
            您分发名单时遇到如下错误：
          </div>
          <div style="margin-bottom: 16px">
            <a-icon type="close-circle-o" style="color: #f5222d; margin-right: 8px"/>
            {{handoutResult?handoutResult.error:'未知错误'}}
            <a style="margin-left: 16px" @click="prevStep">立即解决 <a-icon type="right" /></a>
          </div>
          <!-- <div>
            <a-icon type="close-circle-o" style="color: #f5222d; margin-right: 8px"/>
            您的账户还不具备申请资格
            <a style="margin-left: 16px">立即升级 <a-icon type="right" /></a>
          </div> -->
        </div>
      </Result>
    </a-card>
</template>

<script>
import ACard from 'ant-design-vue/lib/card'
import AButton from 'ant-design-vue/lib/button'
import AIcon from 'ant-design-vue/lib/icon'
import Result from '@/components/Result/Result'
import {mixin} from '@/utils/mixin.js';
import {mapActions} from 'vuex'
export default {
  name: 'Error',
  components: {ACard,AButton,AIcon,Result},
  mixins:[mixin],
  data () {
    return {
      title: '分发失败',
      description: '请核对并修改以下信息后，再重新分发。'
    }
  },
  methods:{
    ...mapActions(['setCurrent']),
      prevStep(){
          this.setCurrent(this.current - 1)
      }
  }
}
</script>

<style scoped>

</style>



