<template>
  <div class="handoutList">
    <a-card title="筛选条件" :bordered="false">
      <p>
        <a-alert type="info" :show-icon="true" style="margin-bottom:16px">
          <div slot="message">
            根据以下条件已筛选出&nbsp;<a style="font-weight: 600">{{listNum}}</a>&nbsp;份待分发名单&nbsp;&nbsp;
            <!-- <a style="margin-left: 24px">清空</a> -->
          </div>
        </a-alert>
        <div style="background: #fafafa; padding: 24px 40px; border-radius: 2px; text-align: left;margin-bottom: 16px">
        <DetailList size="small" :col="2" slot="headerContent">
          <DetailListItem v-for="item in list" :key="item.key" :term="item.label">{{item.value}}</DetailListItem>
        </DetailList>
        </div>
        <a-alert v-if="isWarning" type="warning" banner >
          <div slot="message">
            请注意！您未选择&nbsp;<a style="font-weight: 600;color:red">归属于</a>&nbsp;，将对系统中所有工号的名单进行操作！
          </div>
        </a-alert>
      </p>
    </a-card>
    <a-card title="分发" :bordered="false">
      <a-form :form="form">
        <a-row>
          <a-col class="gutter-row" :xl="12" :lg="12" :md="24">
            <a-form-item label="待分发名单数">
              <a-input :disabled="true" v-decorator="['handout_wait',{initialValue:listNum}]" />
            </a-form-item>
          </a-col>
          <a-col class="gutter-row" :xl="12" :lg="12" :md="24">
            <a-form-item label="分配数量">
              <my-number-input v-decorator="['per_number',{initialValue:0}]" />
            </a-form-item>
          </a-col>
          <a-col class="gutter-row" :xl="12" :lg="12" :md="24" style="display:flex">
            <a-form-item label="分发人员" style="flex:1">
                <!-- <a-tooltip placement="topLeft" :title="form.getFieldValue('userids')"> -->
                  <a-select v-decorator="['userids']" mode="tags" :tokenSeparators="[',']" style="width: 100%" @change="handleChange" placeholder="请选择分发人员">
                  </a-select>
                <!-- </a-tooltip> -->
            </a-form-item>
            <div style="line-height:39.999px">
              <a-button @click="showTransfer" style="margin-left:5px" type="primary">批量选择</a-button>
            </div>
          </a-col>
  
        </a-row>
        <a-form-item class="special-form-item">
          <!-- `autoFormCreate` just `decorator` then first children，如果需要支持多个子节点可以使用JSX。此处本是一个子节点，所以用一个容器包一下就可以了。 -->
          <div class="form-item-container">
            <div :style="{ borderBottom: '1px solid #E9E9E9' }">
              <a-checkbox :indeterminate="indeterminate" @change="onCheckAllChange" :checked="checkAll" v-decorator="['checkedList',{initialValue:checkedList}]">
                选择全部
              </a-checkbox>
            </div>
            <a-checkbox-group :options="plainOptions" v-model="checkedList" @change="onChange" />
          </div>
        </a-form-item>
        <a-form-item :wrapperCol="{span: 19, offset: 5}">
          <a-button :loading="loadingHandout" style="margin-right: 8px" type="primary" @click="handoutConfirm">分发</a-button>
          <a-button :loading="loadingDelete" style="margin-right: 8px" @click="deleteConfirm">删除</a-button>
          <a-button @click="prevStep">上一步</a-button>
        </a-form-item>
      </a-form>
      <transferModal v-if="isRender" :getStaffData="staffData" ref="dialogtreeTransfer" @setPerNumValue="setPerNumValue" @getChildData="getTagsData"></transferModal>
    </a-card>
  </div>
</template>

<script>
  import AForm from "ant-design-vue/lib/form";
  import ARow from "ant-design-vue/lib/row";
  import ACol from "ant-design-vue/lib/col";
  import AInput from "ant-design-vue/lib/input";
  import AButton from "ant-design-vue/lib/button";
  import ACard from "ant-design-vue/lib/card";
  import AAlert from "ant-design-vue/lib/alert";
  import ACheckbox from "ant-design-vue/lib/checkbox";
  import ASelect from "ant-design-vue/lib/select";
  import AIcon from "ant-design-vue/lib/icon";
  import AInputNumber from "ant-design-vue/lib/input-number";
  import ATooltip from 'ant-design-vue/lib/tooltip'
  import {
    mixin
  } from "@/utils/mixin.js";
  import DetailList from "@/components/Tools/DetailList";
  import transferModal from "./Transfer";
  import MyNumberInput from './MyNumberInput'
  import {
    mapActions
  } from 'vuex'
  
  const DetailListItem = DetailList.Item;
  const AFormItem = AForm.Item;
  // const TabPane = Tabs.TabPane;
  const plainOptions = [
    "是否删除算价记录",
    "是否删除跟踪记录",
    "是否更改状态为首拨"
  ];
  const defaultCheckedList = [];
  const ACheckboxGroup = ACheckbox.Group;
  const ASelectOption = ASelect.Option;
  const ASelectOptGroup = ASelect.OptGroup;

  export default {
    components: {
      AForm,
      AFormItem,
      ARow,
      ACol,
      AInput,
      AButton,
      ACard,
      AAlert,
      ACheckbox,
      AIcon,
      ACheckboxGroup,
      ASelect,
      ASelectOption,
      ASelectOptGroup,
      AInputNumber,
      ATooltip,
      DetailList,
      DetailListItem,
      transferModal,
      MyNumberInput
    },
    mixins: [mixin],
    data() {
      return {
        random: Math.floor(Math.random() * 10000),
        tags: [],
        isRender: false,
        loadingHandout: false,
        loadingDelete: false,
        handoutData: [],
        staffData: [],
        //复选框
        checkedList: defaultCheckedList,
        indeterminate: true,
        checkAll: false,
        plainOptions,
        transferValue: []
      };
    },
    beforeCreate() {
      this.form = this.$form.createForm(this);
    },
    created() {
      this.init();
    },
    mounted() {
      this.isRender = true;
    },
    computed:{
      isWarning(){
         return !this.list.filter(item => item.key === 'user_attach').length
      },
      successMsg(){
        return '本次操作，有 ' + (this.handoutResult?this.handoutResult.accountTotal:0) + '份名单被删除， ' + (this.handoutResult?this.handoutResult.accountTrackTotal:0) + ' 条跟踪记录被删除， ' + (this.handoutResult?this.handoutResult.caculatetotal:0) + ' 条算价记录被删除。'
      },
      errorMsg(){
        return '失败原因：'+ (this.handoutResult?this.handoutResult.error:'未知错误')
      }
    },
    methods: {
      ...mapActions(['setPerNum','setHandoutSuccess','setHandoutResult']),
      openSuccessNotification () {
        this.$notification.open({
          message: '删除成功',
          description: this.successMsg,
          icon: <a-icon type="smile" style="color: #108ee9" />,
        });
      },
      openErrorNotification () {
        this.$notification.open({
          message: '删除失败',
          description: this.errorMsg,
          icon: <a-icon type="frown" style="color: #108ee9" />,
        });
      },
      setPerNumValue(value){
        this.form.setFieldsValue({per_number:value})
        // this.setPerNum(value.number)
        // this.normalizePerNum(value)
      },
      // normalizePerNum(e) {
      //   // const {
      //   //   value
      //   // } = e.target
      //   const reg = /^-?(0|[1-9][0-9]*)(\.[0-9]*)?$/
      //   if ((!isNaN(e) && reg.test(e)) || e === '') {
      //     this.setPerNum(e)
      //   } else {
      //     e = this.perNum
      //   }
      //   return e
      // },
      // perNumChange(e) {
      //   // const value = this.form.getFieldValue('per_number')
      //   const {
      //     value
      //   } = e.target
      //   const reg = /^-?(0|[1-9][0-9]*)(\.[0-9]*)?$/
      //   if ((!isNaN(value) && reg.test(value)) || value === '' || value === '-') {
      //     this.setPerNum(value)
      //   }else{
      //     this.form.setFieldsValue({
      //     per_number: this.perNum
      //   })
      //   }
      // },
      getTagsData(tags) {
        this.transferValue = []
        tags.forEach(item => {
            this.transferValue.push(item.name);
        });
        this.form.setFieldsValue({
          userids: this.transferValue
        });
      },
      showTransfer() {
        this.$refs.dialogtreeTransfer.showModal();
      },
      // callback(val) {
      //   console.log(val);
      // },
      // tabsChange() {
      //   console.log(this.activeKey);
      //   this.mockData = []
      //   this.getMock(Number(this.activeKey-1))
      // },
      nextStep() {
        this.$emit("nextStep");
      },
      dataHandle(values) {
        const data = {};
        for (let item in values) {
          if (!values[item]) {
            delete values[item];
          } else if (item === "checkedList") {
            // plainOptions.forEach(itemChild => {
            data.delete_caculate_record = values[item].includes(
              "是否删除算价记录"
            )
            data.delete_account_track_record = values[item].includes(
              "是否删除跟踪记录"
            )
            data.update_status_first_dail = values[item].includes(
              "是否更改状态为首拨"
            )
          }
        }
        if (this.where) {
          data.where = JSON.stringify(this.where)
        }
        return data;
      },
      handout() {
        // e.preventDefault();
        let _this = this;
        return new Promise((resolve, reject) => {
          _this.form.validateFields((err, values) => {
            if (!err) {
              let handoutData = _this.dataHandle(values);
              if (values.userids) {
                handoutData.userids = values.userids
              }
              if(values.per_number){
                handoutData.per_number = values.per_number.number
              }
              _this.$api.handoutList(handoutData).then((result) => {
                resolve(result)
              }).catch((err) => {
                reject(err)
              });
            } else {
              _this.$notification['warning']({
                  message: err,
                })
            }
          });
        })
      },
      deleteList() {
        let _this = this;
        return new Promise((resolve, reject) => {
          _this.form.validateFields((err, values) => {
            _this.loadingDelete = false;
            if (!err) {
              let handoutData = _this.dataHandle(values);
              handoutData.per_number = values.handout_wait || 0
              _this.$api.deleteList(handoutData).then((result) => {
                resolve(result)
              }).catch((err) => {
                reject(err)
              });
            } else {
              _this.$notification['warning']({
                  message: err,
                })
            }
          });
        })
      },
      prevStep() {
        this.$emit("prevStep");
      },
      deleteConfirm() {
        let _this = this;
        this.loadingDelete = true;
        this.$confirm({
          title: "删除确认",
          content: "您确定要删除筛选出的"+this.listNum+"条名单吗？",
          okText: "删除",
          okType: "danger",
          cancelText: "取消",
          onOk() {
            _this.deleteList().then((result) => {
              _this.setHandoutResult({
                accountTotal:result.data.accountTotal,
                accountTrackTotal:result.data.accountTrackTotal,
                caculatetotal:result.data.caculatetotal
              })
              _this.openSuccessNotification()
            }).catch((err) => {
              let errMsg
              if (err.response) {
                errMsg = err.response.data.error
              }
              _this.setHandoutResult({
                error:errMsg
              })
              _this.openErrorNotification()
            });;
          },
          onCancel() {
            _this.loadingDelete = false;
          }
        });
      },
      onChange(checkedList) {
        this.indeterminate = !!checkedList.length && checkedList.length < plainOptions.length;
        this.checkAll = checkedList.length === plainOptions.length;
      },
      onCheckAllChange(e) {
        Object.assign(this, {
          checkedList: e.target.checked ? plainOptions : [],
          indeterminate: false,
          checkAll: e.target.checked
        });
      },
      handoutConfirm() {
        let _this = this;
        this.loadingHandout = true;
        this.$confirm({
          title: "分发确认",
          content: "您确定要分发这些名单",
          okText: "分发",
          okType: "primary",
          cancelText: "取消",
          onOk() {
            _this.handout().then((result) => {
              _this.loadingHandout = false;
              _this.setHandoutSuccess(true)
              _this.setHandoutResult({
                accountTotal:result.data.accountTotal,
                accountTrackTotal:result.data.accountTrackTotal,
                caculatetotal:result.data.caculatetotal
              })
              _this.nextStep();
            }).catch((err) => {
              let errMsg
              if (err.response) {
                errMsg = err.response.data.error
              }
              _this.loadingHandout = false;
              _this.setHandoutSuccess(false)
              _this.setHandoutResult({
                error:errMsg
              })
              _this.nextStep();
            });;
          },
          onCancel() {
            _this.loadingHandout = false;
          }
        });
      },
      // getGroupMock() {
      //   const group = [];
      //   for (let i = 0; i < 10; i++) {
      //     const groupData = {
      //       key: (i + 1).toString(),
      //       groupName: `小组${i + 1}`
      //     };
      //     group.push(groupData);
      //   }
      //   this.group = group;
      // },
      // getMock(group) {
      //   // const targetKeys = [];
      //   const mockData = [];
      //   const start=group*5
      //   const end=(group+1)*5
      //   for (let i=start; i < end; i++) {
      //     const data = {
      //       key: i.toString(),
      //       title: `姓名${i}`,
      //       description: `姓名${i + 1}`,
      //       // chosen: Math.random() * 2 > 1
      //     };
      //     // if (data.chosen) {
      //     //   targetKeys.push(data.key);
      //     // }
      //     mockData.push(data);
      //   }
  
      //   this.mockData = mockData;
      //   // this.targetKeys = targetKeys;
      // },
      // filterOption(inputValue, option) {
      //   return option.description.indexOf(inputValue) > -1;
      // },
      // handleChange(targetKeys, direction, moveKeys) {
      //   this.targetKeys = [...this.targetKeys,...targetKeys]
      // },
      handleChange(value) {
        this.transferValue = value;
      },
      init() {
        this.handoutData = [{
            id: 0,
            field: "handout_wait",
            label: "待分发名单数",
            value: this.listNum,
            type: "input",
            options: [],
            disabled: true
          },
          {
            id: 1,
            field: "per_number",
            label: "分配数量",
            value: "",
            type: "input",
            options: [],
            disabled: false
          },
          {
            id: 2,
            field: "userids",
            label: "分发人员",
            value: ["a", "c"],
            type: "transfer",
            options: [],
            disabled: false
          }
        ];
      }
    }
  };
</script>

<style scoped>
  
</style>


<style>
  .handoutList div.ant-card-head {
    background-color: white;
  }
  .handoutList div.ant-form-item-control-wrapper{
    overflow: hidden;
  }
/*   
  .handoutList div.detail-list {
    margin-bottom: 16px;
  } */
  
  .handoutList div.ant-form-item-label {
    width: 100px;
  }
  
  .special-form-item .ant-form-item-control-wrapper {
    text-align: left;
  }
  
  
  /* .ant-select-dropdown-menu-item-selected:hover:after{
              display: none
            } */
</style>



