<template>
  <page-layout :title="title">
  <div class="handout">
    <a-card :bordered="false">
      <a-steps
        class="steps"
        :current="current"
      >
        <a-step title="筛选名单" />
        <a-step title="分发名单" />
        <a-step title="分发结果" />
      </a-steps>
      <div class="content">
        <filtrate
          v-if="Number(current) === 0"
          @nextStep="nextStep"
        ></filtrate>
        <handoutList
          v-if="Number(current) === 1"
          @nextStep="nextStep"
          @prevStep="prevStep"
        ></handoutList>
        <handoutResult
          v-if="Number(current) === 2"
        ></handoutResult>
      </div>
    </a-card>
  </div>
</page-layout>
</template>

<script>
import filtrate from "./Filtrate";
import handoutList from "./HandoutList";
import handoutResult from "./HandoutResult";
import ACard from 'ant-design-vue/lib/card'
import ASteps from 'ant-design-vue/lib/steps'
import PageLayout from "@/components/Page/PageLayout";
import {mixin} from '@/utils/mixin.js'
import {mapActions} from 'vuex'
const AStep = ASteps.Step;
export default {
  components: {
    filtrate,
    handoutList,
    handoutResult,
    ACard,
    ASteps,
    AStep,
    PageLayout
  },
  mixins:[mixin],
  data() {
    return {
      description: "将一个冗长或用户不熟悉的表单任务分成多个步骤，指导用户完成。",
    };
  },
  computed:{
    title() {
      return this.$route.meta.title;
    },
  },
  methods: {
    ...mapActions(['setCurrent']),
    nextStep() {
      if (this.current < 2) {
        this.setCurrent(this.current + 1);
      }
    },
    prevStep() {
      if (this.current > 0) {
        this.setCurrent(this.current - 1);
      }
    },
    finish() {
      this.setCurrent(0);
    }
  }
};
</script>

<style lang="less" scoped>
div.content div.page-header-index-wide>div.handout{
  padding: 0px
}
.steps {
  max-width: 1450px;
  margin: 16px auto;
}
.content {
  max-width: 1340px;
  margin: 40px auto;
}
</style>



