<template>
  <a-modal class="transferModal" title="分发人员" :visible="visible" @ok="handleOk" :maskClosable="maskClosable" @cancel="handleCancel" width="850px">
    <a-alert type="info" :show-icon="true">
      <div slot="message">
        当前有&nbsp;<a style="font-weight: 600">{{targetKeys.length}}</a>&nbsp;名员工参与分发，共
        <a style="font-weight: 600">{{listNum}}</a>份名单可分配，每人分配&nbsp;
        <a style="font-weight: 600">{{perNum?perNum:0}}</a>&nbsp;份，剩余&nbsp;
        <a style="font-weight: 600">{{targetKeys.length?((listNum-targetKeys.length*perNum)>0?(listNum-targetKeys.length*perNum):0):listNum}}</a>&nbsp;份。
        <a style="margin-left: 24px" @click="clearBoth">清空</a>
        <a style="margin-left: 24px" @click="maximized_average_distribution">{{isMaximized?'取消按人员最大化平均分配':'按人员最大化平均分配'}}</a>
      </div>
    </a-alert>
    <a-row>
      <a-col class="gutter-row" :xl="3" :lg="12" :md="24">
        <a-tabs defaultActiveKey="1" v-model="activeKey" :tabPosition="device == 'mobile' ? 'top' : 'left'" :style="{ height: device == 'mobile' ?'60px':'300px'}" @change="tabsChange" @prevClick="callback" @nextClick="callback">
          <a-tab-pane v-for="item in mockData" :tab="item.groupName" :key="item.key"></a-tab-pane>
        </a-tabs>
      </a-col>
      <a-col class="gutter-row" :xl="11" :lg="24" :md="24">
        <a-transfer :dataSource="staffData" :listStyle="{
          width: device == 'mobile'?'130px':'150px',
          height: '300px',
          marginBottom:'16px'
          }" showSearch :filterOption="filterOption" :targetKeys="targetKeys" @change="handleChange" :render="item=>item.title">
        </a-transfer>
      </a-col>
      <a-col class="gutter-row" :xl="10" :lg="24" :md="24">
        <a-card title="已选员工">
          <p>
            <template v-for="tag in tags">
              <a-tooltip
                v-if="tag.length > 20"
                :key="tag.key"
                :title="tag.name"
              >
                <a-tag
                  :key="tag.key"
                  closable
                  :afterClose="() => handleClose(tag)"
                >
                  {{`${tag.name.slice(0, 20)}...`}}
                </a-tag>
              </a-tooltip>
              <a-tag
                v-else
                :key="tag.key"
                closable
                :afterClose="() => handleClose(tag)"
              >
                {{tag.name}}
              </a-tag>
</template>
                </p>
            </a-card>
        </a-col>

    </a-row>
  </a-modal>
</template>

<script>
  import AModal from 'ant-design-vue/lib/modal'
  import ATabs from 'ant-design-vue/lib/tabs'
  import ATransfer from 'ant-design-vue/lib/transfer'
  import AAlert from 'ant-design-vue/lib/alert'
  import ARow from 'ant-design-vue/lib/row'
  import ACol from 'ant-design-vue/lib/col'
  import ATooltip from 'ant-design-vue/lib/tooltip'
  import ATag from 'ant-design-vue/lib/tag'
  import ACard from 'ant-design-vue/lib/card'
  import {
    mixin,
    mixinDevice
  } from '@/utils/mixin.js'
  import {
    mapActions
  } from 'vuex'
  const ATabPane = ATabs.TabPane;
  export default {
    components: {
      AModal,
      ATabs,
      ATransfer,
      ATabPane,
      AAlert,
      ARow,
      ACol,
      ATooltip,
      ATag,
      ACard
    },
    mixins: [mixin,mixinDevice],
    data() {
      return {
        maskClosable: false,
        staffData: [],
        staffIntegrate: [],
        visible: false,
        //穿梭框
        mockData: [],
        targetKeys: [],
        selectedGroup: "",
        activeKey: "0",
        count: 0,
        tags: [],
        isMaximized:false,
      };
    },
    watch: {
      targetKeys: {
        handler: function(targetKeys) {
          let _this = this
          const data = [];
          this.targetKeys.forEach(item => {
            data.push({
              key: item,
              name: _this.staffIntegrate.filter(itemChild => itemChild.key == item)[0].title
            });
          });
          this.tags = data;
          if(this.isMaximized){
            this.maximized_average_distribution_compute()
          }
        }
      },
    },
    created() {
      this.getMock(); //默认第一组
      this.staffData = this.mockData[Number(this.activeKey)].staffs
      // this.getGroupMock();
    },
    mounted() {},
    methods: {
      ...mapActions(['setPerNum']),
      //最大化平均分配
      maximized_average_distribution() {
        this.isMaximized = !this.isMaximized
        if(this.isMaximized){
          this.maximized_average_distribution_compute()
        }else{
          // this.setPerNum()
        }
      },
      maximized_average_distribution_compute(){
        const perNumValue =  this.targetKeys.length?parseInt(this.listNum / this.targetKeys.length):0
        this.setPerNum(perNumValue)
        // this.$emit('setPerNumValue',perNumValue)
      },
      showModal() {
        this.visible = true;
      },
      callback(val) {
      },
      tabsChange() {
        this.staffData = this.mockData[Number(this.activeKey)].staffs
      },
      getGroupMock() {
        const group = [];
        for (let i = 0; i < 10; i++) {
          const groupData = {
            key: (i + 1).toString(),
            groupName: `小组${i + 1}`
          };
          group.push(groupData);
        }
        //   this.$props.getStaffData.forEach(item => {
        //       const groupData = {
        //           key:item.key,
        //           groupName:item.group
        //       }
        //       group.push(groupData);
        //   });
        this.group = group;
      },
      getMock() {
        // const targetKeys = [];
        const mockData = [];
        for (let i = 0; i < 10; i++) {
          let groups = {
            key: i.toString(),
            groupName: `小组${i}`,
            staffs: []
          }
          for (let j = 0; j < 10; j++) {
            groups.staffs.push({
              key: this.count.toString(),
              title: `${this.count}(姓名${this.count})`,
              description: `${this.count}(姓名${this.count})`
              // chosen: Math.random() * 2 > 1
            })
            // if (data.chosen) {
            //   targetKeys.push(data.key);
            // }
            this.staffIntegrate.push({
              key: this.count.toString(),
              title: `${this.count}(姓名${this.count})`,
              description: `${this.count}(姓名${this.count})`
              // chosen: Math.random() * 2 > 1
            })
            this.count++
          }
  
          mockData.push(groups);
        }
  
        //   this.$props.getStaffData[group].staffs.forEach(item => {
        //       const data = {
        //           key:item.key,
        //           title:item.name,
        //           description:item.description
        //       }
        //       mockData.push(data);
        //   });
  
        this.mockData = mockData;
        // this.targetKeys = targetKeys;
      },
      filterOption(inputValue, option) {
        return option.description.indexOf(inputValue) > -1;
      },
      handleChange(targetKeys, direction, moveKeys) {
        this.targetKeys = targetKeys;
        // this.maximized_average_distribution()
      },
      handleCancel(e) {
        this.visible = false;
      },
      handleOk(e) {
        this.visible = false;
        this.$emit("getChildData", this.tags)
        this.$emit('setPerNumValue',{number:this.perNum})
      },
      //删除tag
      handleClose(removedTag) {
        const tags = this.tags.filter(tag => tag !== removedTag);
        const targetKeys = this.targetKeys.filter(
          targetKey => targetKey !== removedTag.key
        );
        this.tags = tags;
        this.targetKeys = targetKeys;
      },
      clearBoth() {
        this.tags = [];
        this.targetKeys = [];
        this.$emit('setPerNumValue',{number:0})
      }
    }
  };
</script>

<style>
  .transfer-container {
    display: flex;
    padding: 16px 0;
  }
  
  @media (min-width: 1200px) {
    .ant-transfer {
      padding-left: 10px;
    }
    .transferModal .ant-card-body {
      height: 266px;
      overflow: scroll;
    }
  }
  
  .transferModal .ant-modal-body>div {
    margin-bottom: 16px;
  }
  
  .transferModal .ant-card-head {
    padding: 0 12px;
    min-height: 34px;
  }
  
  .transferModal .ant-card-head-title {
    padding: 6px 0;
    line-height: 1.5;
    font-size: 14px;
  }
  
  .transferModal .ant-card-bordered {
    border-radius: 4px;
  }
  
  .transferModal .ant-card-body {
    padding: 6px 12px;
  }
  
  .transferModal .ant-card-body>p>div.ant-tag {
    margin-bottom: 6px;
  }
  
  :root .ant-tabs-tab-prev-icon:before,
   :root .ant-tabs-tab-next-icon:before {
    font-size: 20px
  }
</style>



