<template>
  <a-card :bordered="false">
    <result :is-success="true" :description="description" :title="title">
      <template slot="action">
        <a-button type="primary" @click="continueHandout">继续分发</a-button>
        <!-- <a-button style="margin-left: 8px">查看项目</a-button>
        <a-button style="margin-left: 8px">打印</a-button> -->
      </template>
      <div>
        <div style="font-size: 16px; color: rgba(0, 0, 0, 0.85); font-weight: 500; margin-bottom: 20px;">分发结果</div>
        <a-row style="margin-bottom: 16px">
          <a-col :xs="24" :sm="12" :md="12" :lg="12" :xl="6">
            <span style="color: rgba(0, 0, 0, 0.85)">操作人：</span>
            20180724089
          </a-col>
          <a-col :xs="24" :sm="12" :md="12" :lg="12" :xl="6">
            <span style="color: rgba(0, 0, 0, 0.85)">操作类型：</span>
            分发
          </a-col>
          <a-col :xs="24" :sm="24" :md="24" :lg="24" :xl="12">
            <span style="color: rgba(0, 0, 0, 0.85)">操作时间：</span>
            2016-12-12 ~ 2017-12-12
          </a-col>
        </a-row>
        <a-alert type="info" :show-icon="true" style="margin-bottom:16px">
          <div slot="message">
            本次操作，有&nbsp;<a style="font-weight: 600">{{handoutResult?handoutResult.accountTotal:0}}</a>&nbsp;份名单被分发，
            <a style="font-weight: 600">{{handoutResult?handoutResult.accountTrackTotal:0}}</a>&nbsp;条跟踪记录被删除，
            <a style="font-weight: 600">{{handoutResult?handoutResult.caculatetotal:0}}</a>&nbsp;条算价记录被删除。
            <!-- <a style="margin-left: 24px">清空</a> -->
          </div>
        </a-alert>
      </div>
    </result>
  </a-card>
</template>

<script>
  import Result from '@/components/Result/Result'
  import ACard from 'ant-design-vue/lib/card'
  import AButton from 'ant-design-vue/lib/button'
  import ARow from 'ant-design-vue/lib/row'
  import ACol from 'ant-design-vue/lib/col'
  import AAlert from 'ant-design-vue/lib/alert'
  import { mixinDevice,mixin } from '@/utils/mixin.js'
  import {mapActions} from 'vuex'

  const directionType = {
    horizontal: 'horizontal',
    vertical: 'vertical'
  }

  export default {
    name: 'Success',
    components: {
      Result,
      ACard,
      AButton,
      ARow,
      ACol,
      AAlert
    },
    mixins: [mixinDevice,mixin],
    data () {
      return {
        title: '分发成功',
        description: '提交结果页用于反馈一系列操作任务的处理结果，\n' +
          ' 如果仅是简单操作，使用 Message 全局提示反馈即可。\n' +
          ' 本文字区域可以展示简单的补充说明，如果有类似展示\n' +
          ' “单据”的需求，下面这个灰色区域可以呈现比较复杂的内容。',
        directionType
      }
    },
    methods:{
        ...mapActions(['setCurrent']),
        continueHandout(){
            this.setCurrent(0)
        }
    }
  }
</script>

<style scoped>

</style>


