  import AForm from "ant-design-vue/lib/form";
  import ARow from "ant-design-vue/lib/row";
  import ACol from "ant-design-vue/lib/col";
  import AInput from "ant-design-vue/lib/input";
  import ASelect from "ant-design-vue/lib/select";
  import ADatePicker from "ant-design-vue/lib/date-picker";
  import AButton from "ant-design-vue/lib/button";
  import ACard from "ant-design-vue/lib/card";
  import {
    mapMutations,
    mapActions
  } from "vuex";
  import { mixin } from '@/utils/mixin.js'
  import moment from 'moment'
  const AFormItem = AForm.Item;
  const ASelectOption = ASelect.Option;
  const {
    RangePicker,
    MonthPicker
  } = ADatePicker;
  export default {
    components: {
      AForm,
      AFormItem,
      ARow,
      ACol,
      AInput,
      ASelect,
      ASelectOption,
      ADatePicker,
      RangePicker,
      MonthPicker,
      AButton,
      ACard
    },
    mixins:[mixin],
    data() {
      return {
        accounts_dictionary: this.$localstorage.getLocalStorage("accounts_dictionary") || null,
        filtrateData: [],
        whereTemp:[],
      };
    },
    beforeCreate() {
      this.form = this.$form.createForm(this);
    },
    created() {
      if(!this.accounts_dictionary){
        this.getDictionaryFromServer()
      }
      this.init();
      
    },
    methods: {
      moment,
      ...mapActions(['setList','setListNum','setWhere','setCacheData']),
      ...mapMutations({
        setList: "LIST"
      }),
      getDictionaryFromServer() {
      let _this = this;
      return new Promise(function(resolve, reject) {
        const dictionaryName = "accounts";
        _this.$api
          .getDictionary(dictionaryName)
          .then(result => {
            _this.accounts_dictionary = result.data
            _this.$localstorage.putLocalStorage(
              "accounts_dictionary",
              result.data
            );
            resolve(result);
          })
          .catch(err => {
            _this.$myNotification.sendFailedNotification(err)
            reject(err);
          });
      });
    },
      nextStep(e) {
        e.preventDefault();
        let _this = this;
        this.form.validateFields((err, values) => {
          if (!err) {
            values.created_at = values.created_at ? [
                values.created_at[0].format("YYYY-MM-DD HH:mm:ss"),
                values.created_at[1].format("YYYY-MM-DD HH:mm:ss")
              ] :
              undefined;
            values.business_expiration_date = values.business_expiration_date ? [
                values.business_expiration_date[0].format(
                  "YYYY-MM-DD HH:mm:ss"
                ),
                values.business_expiration_date[1].format("YYYY-MM-DD HH:mm:ss")
              ] :
              undefined;
            values.register_date = values.register_date ? [
                values.register_date[0].format("YYYY-MM-DD HH:mm:ss"),
                values.register_date[1].format("YYYY-MM-DD HH:mm:ss")
              ] :
              undefined;
            // values.register_month = values.register_month ?
            //   values.register_month.format("MM") :
            //   undefined;
            const list = [];
            const where = [];
            for (let item in values) {
              if (!values[item]) {
                delete values[item];
              } else {
                const target = _this.filtrateData.filter(
                  elm => elm.field === item
                )[0];
                if (item === 'business_expiration_date' || item === 'created_at' || item === 'register_date') {
                  where.push({
                    field: item,
                    condition: ">=",
                    value: values[item][0],
                    link: "and"
                  })
                  where.push({
                    field: item,
                    condition: "<=",
                    value: values[item][1],
                    link: "and"
                  })
                  list.push({
                  key: item,
                  value: values[item][0] + " ~ " + values[item][1],
                  label: target.label
                });
                }else if(item === 'register_month'){
                  where.push({
                    field: item,
                    condition: "=",
                    value: values[item]?values[item].format("MM") : undefined,
                    link: "and"
                  });
                  values.register_month = values.register_month ?
                  values.register_month.format("YYYY-MM") :
                  undefined;
                } else {
                  where.push({
                    field: item,
                    condition: "=",
                    value: values[item],
                    link: "and"
                  });
                  if(item==='status'){
                    for (let elm in _this.accounts_dictionary.status){
                      if(elm === values[item]){
                        list.push({
                          key: item,
                          value:_this.accounts_dictionary.status[elm],
                          label: target.label
                        }); 
                      }
                    }
                  }else if(item==='batch'){
                    for (let elm in _this.accounts_dictionary.batch){
                      if(elm === values[item]){
                        list.push({
                          key: item,
                          value:_this.accounts_dictionary.batch[elm],
                          label: target.label
                        }); 
                      }
                    }
                  }else if(item==='type'){
                    for (let elm in _this.accounts_dictionary.type){
                      if(elm === values[item]){
                        list.push({
                          key: item,
                          value:_this.accounts_dictionary.type[elm],
                          label: target.label
                        }); 
                      }
                    }
                  }else{
                    list.push({
                      key: item,
                      value:values[item],
                      label: target.label
                    });
                  }
                }
              }
            }
            _this.whereTemp = where
            _this.setCacheData(values)
            _this.setList(list);
            _this.$api
              .getHandoutList({
                where: JSON.stringify(_this.whereTemp)
              })
              .then((result) => {
                _this.setListNum(result.data)
                _this.setWhere(_this.whereTemp)
              })
              .catch(err => {
                _this.setListNum(0)
                _this.setWhere([])
                _this.$myNotification.sendFailedNotification(err)
              });
            _this.$emit("nextStep");
          }
        });
      },
      resetForm() {
        this.form.resetFields();
      },
