            // options: []
          },
          {
            id: 8,
            field: "type",
            label: "类型",
            // value: "",
            // type: "select",
            // options: []
          },
          {
            id: 9,
            field: "status",
            label: "状态",
            // value: "",
            // type: "select",
            // options: {
            //   FIRST_DIAL: "首拨",
            //   FAILED: "失败",
            //   INVALID: "无效",
            //   APPOINTMENT_QUOTATION: "预约已报价",
            //   APPOINTMENT_NON_QUOTATION: "预约未报价",
            //   SUCCESS: "成功"
            // }
          },
          {
            id: 10,
            field: "created_at",
            label: "创建时间",
            // value: "",
            // type: "range",
            // options: []
          },
          {
            id: 11,
            field: "business_expiration_date",
            label: "商业到期",
            // value: "",
            // type: "range",
            // options: []
          },
          {
            id: 12,
            field: "register_date",
            label: "注册日期",
            // value: "",
            // type: "rangeNoTime",
            // options: []
          },
          {
            id: 13,
            field: "register_month",
            label: "注册月份",
            // value: "",
            // type: "month",
            // options: []
          }
        ];
        // this.filtrateData = [{
        //     id: 0,
        //     field: "user_attach",
        //     label: "归属于",
        //     value: "",
        //     type: "input",
        //     options: []
        //   },
        //   {
        //     id: 1,
        //     field: "user_create",
        //     label: "创建人",
        //     value: "",
        //     type: "input",
        //     options: []
        //   },
        //   {
        //     id: 2,
        //     field: "team",
        //     label: "品牌团队",
        //     value: "",
        //     type: "input",
        //     options: []
        //   },
        //   {
        //     id: 3,
        //     field: "model",
        //     label: "品牌型号",
        //     value: "",
        //     type: "input",
        //     options: []
        //   },
        //   {
        //     id: 4,
        //     field: "area",
        //     label: "片区",
        //     value: "",
        //     type: "select",
        //     options: ["成都", "地区", "周边"]
        //   },
        //   {
        //     id: 5,
        //     field: "park",
        //     label: "园区",
        //     value: "",
        //     type: "select",
        //     options: []
        //   },
        //   {
        //     id: 6,
        //     field: "company",
        //     label: "保险公司",
        //     value: "",
        //     type: "select",
        //     options: []
        //   },
        //   {
        //     id: 7,
        //     field: "batch",
        //     label: "批次",
        //     value: "",
        //     type: "select",
        //     options: []
        //   },
        //   {
        //     id: 8,
