<template>
  <div class="handoutResult">
    <my-success v-if="isSuccess"/>
    <my-error v-else/>
  </div>

</template>

<script>
import MyError from './Error'
import MySuccess from './Success'
import {mixin} from '@/utils/mixin.js'
export default {
  mixins:[mixin],
  components: {
    MySuccess,
    MyError
  },
  computed:{
    isSuccess(){
      return this.handoutSuccess
    }
  }
};
</script>

<style>
</style>



